 props = defineProps<{\n\thandleItemCheckEvent: (item: ITableData) => void;\n\tstartScreen: IModel | undefined;\n\tversionDialog: (item: ITableData) => void;\n\thandleCollapse: (item: ITableData, childIterate?: boolean) => void;\n\titem: ITableData;\n\tmoduleVersionDialog: (item: ITableData) => void;\n}>()\n\nconst chevronClass = ref(\"mdi mdi-menu-down chevron\");\nconst checked = ref(props.item.checked);\n\nfunction calculateClass() {\n\tprops.handleCollapse(props.item, true);\n\tchevronClass.value = `mdi mdi-menu${props.item.collapsed ? '-right' : '-down'} chevron`\n};\n\nfunction handleCheck() {\n\tprops.handleItemCheckEvent(props.item);\n\tchecked.value = props.item.checked;\n\treturn checked.value\n}\n\nfunction handleVersionDialog() {\n\tif (props.item.imported) {\n\t\tprops.moduleVersionDialog(props.item);\n\t} else {\n\t\tprops.versionDialog(props.item);\n\t}\n}\n\nconst iconLeft = computed(() => {\n\tconst level = props.item.level || 0;\n\treturn level * 25;\n});\n\nconst modelNameLeft = computed(() => {\n\tconst level = props.item.level || 0;\n\treturn level * 25;\n});\n\n</script>\n\n<style scoped>\n.row-container {\n\twidth: 100%;\n\theight: 36px;\n\tposition: relative;\n\tbackground: #ffffff 0% 0% no-repeat padding-box;\n\tborder: 1px solid #e0e0e0;\n}\n\n.app-tier-model-icon {\n\tposition: absolute;\n\twidth: 16px;\n\theight: 16px;\n\ttop: 6px;\n}\n\n.app-tier-model-name {\n\tposition: absolute;\n\ttop: 9px;\n\tfont: normal normal normal 14px/19px Roboto;\n\tcolor: #212121;\n}\n\n.app-tier-model-name.disabled {\n\tcolor: #D5D5D5;\n}\n\n.version-text {\n\tmargin: 10px 7px 10px auto;\n\tdisplay: table;\n\tfont: normal normal normal 12px/16px Roboto;\n\tcolor: #757575;\n\tcursor: pointer;\n\tuser-select: none;\n}\n\n.version-text.disabled {\n\tpointer-events: none;\n\tcursor: pointer;\n}\n\n.chevron {\n\tposition: absolute;\n\tcolor: #757575;\n\tfont-size: 24px;\n}\n\n.button-tooltip {\n\tvisibility: hidden;\n\tposition: absolute;\n\tbackground: #757575;